import React from 'react';
import { Line } from 'react-chartjs-2';
import { Chart as ChartJS, CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend } from 'chart.js';
import './App.css';

ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend);

const App = () => {
  const data = {
    labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
    datasets: [
      {
        label: 'Sales',
        data: [65, 59, 80, 81, 56, 55, 40],
        fill: false,
        backgroundColor: 'rgb(75, 192, 192)',
        borderColor: 'rgba(75, 192, 192, 0.2)',
      },
    ],
  };

  const options = {
    maintainAspectRatio: true,
    scales: {
      y: {
        beginAtZero: true,
      },
    },
  };

  return (
    <div className="app">
      <header className="header">
        <h1>My Creative Landing Page</h1>
        <nav>
          <a href="#home">Home</a>
          <a href="#features">Features</a>
          <a href="#testimonials">Testimonials</a>
          <a href="#contact">Contact</a>
        </nav>
      </header>

      <section id="home" className="hero">
        <h2>Welcome to the Future</h2>
        <p>Discover the amazing features and services we offer. Join us today and be a part of something special.</p>
        <button>Get Started</button>
      </section>

      <section id="features" className="features">
        <h1>Features</h1>
        <div className="feature-list">
          <div className="feature">
            <h2>Feature One</h2>
            <p>Amazing feature description that is engaging and informative.</p>
          </div>
          <div className="feature">
            <h2>Feature Two</h2>
            <p>Another amazing feature that will attract and retain users.</p>
          </div>
          <div className="feature">
            <h2>Feature Three</h2>
            <p>The third feature that is just as amazing as the first two.</p>
          </div>
        </div>
      </section>

      <section id="dashboard" className="dashboard">
        <h1>Dashboard</h1>
        <div className="grid">
          <div className="card large">
            <h2>Sales Overview</h2>
            <Line data={data} options={options} />
          </div>
          <div className="card small">
            <h2>Recent Orders</h2>
            <p>Order #12345 - $250</p>
            <p>Order #12346 - $500</p>
            <p>Order #12347 - $750</p>
          </div>
          <div className="card small">
            <h2>Total Revenue</h2>
            <h3>$24,000</h3>
          </div>
          <div className="card small">
            <h2>New Customers</h2>
            <h3>1,200</h3>
          </div>
          <div className="card small">
            <h2>Active Users</h2>
            <h3>5,400</h3>
          </div>
        </div>
      </section>

      <section id="testimonials" className="testimonials">
        <h2>What Our Users Say</h2>
        <div className="testimonial-list">
          <div className="testimonial">
            <p>"This product has changed my life for the better!"</p>
            <p>- Happy Customer</p>
          </div>
          <div className="testimonial">
            <p>"I can't imagine going back to how things were before."</p>
            <p>- Satisfied User</p>
          </div>
          <div className="testimonial">
            <p>"Simply the best service out there. Highly recommend!"</p>
            <p>- Enthusiastic Client</p>
          </div>
        </div>
      </section>

      <footer id="contact" className="footer">
        <h2>Contact Us</h2>
        <p>Email: contact@mywebsite.com</p>
        <p>Phone: +123 456 7890</p>
        <p>2024 © My Creative Landing Page. All rights reserved.</p>
      </footer>
    </div>
  );
};

export default App;